	module.exports = {"data":{"post":{"html":"<p>What's worse than the office building management planning a power shutdown from 11:00 am to 1:00 pm on Saturday (and you have vital systems in the building that need to be restarted)?</p>\n<p>It's when on Thursday the building management decides to change the shutdown to 5:00 to 7:00 pm, and it's perfect grill weather.</p>\n<p>At least I don't have to drive in, since my boss bravely volunteered to turned things back on. I just have to make sure everything works. But I'd rather be grilling.</p>\n<p><strong>Update:</strong> Power was restored 2 hours later than promised, throroughly ruining our evenings. Of course the machines didn't come right up, Nico had to reset the router. One machine <em>still</em> doesn't have power... that has to wait until Monday. Oh joy.</p>","fields":{"title":"Planned Shutdown","date":"28 May 2005","url":"https://papascott.de/2005-05-28-planned-shutdown/"}}},"pathContext":{"slug":"/2005-05-28-planned-shutdown/","prev":{"fields":{"title":"Hot Wheels Gorleben","date":"28 May 2005","slug":"/2005-05-28-hot-wheels-gorleben/"}},"next":{"fields":{"title":"Harbor Tour","date":"29 May 2005","slug":"/2005-05-29-harbor-tour/"}}}}

